import React, { Component } from 'react';
import { Tag } from "antd";
import store from "../store/index";

let colors = ['magenta', 'blue', 'green', 'orange', 'lime', 'cyan']
class TagViews extends Component {
    constructor(props) {
        super(props);
        let { AppInfo } = store.getState().methodReducer
        this.state = {
            tags: AppInfo
        }

        this.unsubscribe = store.subscribe(this._refreshData)

    }

    _refreshData = () => {
        let { AppInfo } = store.getState().methodReducer
        this.setState({
            tags: AppInfo
        })
    }

    componentWillUnmount() {
        this.unsubscribe()
    }

    getTagViews = () => {
        let tags = this.state.tags
        let tagViews = []
        let count = 0
        if (tags) {
            tags.forEach(element => {
                let color = colors[count % colors.length]
                tagViews.push(
                    <Tag style={{ marginBottom: 8 }}
                        color={color}>
                        {element}
                    </Tag>
                )

                count++
            });
        }

        return tagViews
    }

    render() {
        return (
            <div style={{ marginLeft: 25, display: 'inline' }}>
                {this.getTagViews()}
            </div>
        );
    }
}

export default TagViews;