class Mock {

    constructor() {
        this.refreshMock = this.refreshMock.bind(this);
        this.recvFun = null;
    }


    start(recvFun) {
        this.recvFun = recvFun;
        setInterval(this.refreshMock, 2000);
    }

    refreshMock() {
        this.recvFun("appInfo", {
            appName: "I am Name",
            labels: ["label1", "label2", "label3"]
        });
    }

}


export default Mock