import React, { Component } from 'react';
import websocketEngine from './websocket/WebscoketEngine'
import Mock from './Mock'
import { Layout, message } from 'antd';
import LeftSider from './widget/LeftSider'
import { BrowserRouter } from 'react-router-dom'

import RightContent from './widget/RightContent'
import store from "./store/index"
import { updateAppInfo, updateMethodCostInfo } from "./store/actionCreators";
import TagViews from './widget/TagViews';
import Bus from './event/eventBus'

const { Header } = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    this._onReceiveMessage = this._onReceiveMessage.bind(this);
    this.mock = new Mock();
  }

  componentDidMount() {
    websocketEngine.setReceiveMessageCallback(this._onReceiveMessage);
    websocketEngine.start(function (evt) {
      websocketEngine.sendMessage('{"moduleName": "OnlineMessage"}')
    });

    // this._testData()

    this._registerEventBus()
  }

  _registerEventBus = () => {
    Bus.addListener('sendMsgToClient', (msg) => {
      console.log(msg);
      try {
        websocketEngine.start(function (evt) {
          websocketEngine.sendMessage(msg)
        });
      } catch (error) {
        message.error('MethodTraceMan与手机客户端未连接完毕，请稍后再试！')
      }
    });

  }

  componentWillUnmount() {
    Bus.removeAllListeners()
  }
  _testData = () => {
    setTimeout(() => {
      this._onReceiveMessage("appInfo", ["机型：samsung SM-G9700", "系统版本：28", "包名：ctrip.english.debug", "App版本：7.1.0", "UID：E451995153", "ClientID：37002118410181244263"]
      )
    }, 3000)

    setTimeout(() => {
      this._onReceiveMessage("methodCost", [{
        "costTime": 1.0,
        "endPos": 3.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.MainSearchInfoHelper.getInstance.()Lcom.ctrip.ibu.hotel.module.main.MainSearchInfoHelper;",
        "startPos": 0.0
      }, {
        "costTime": 15.0,
        "endPos": 45.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.HotelMainActivity.lambda$gotoHotelList$11.(IILandroid.content.Intent;)V",
        "startPos": 6.0
      }, {
        "costTime": 14.0,
        "endPos": 44.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.HotelMainPresenter.onHotelsActivityResult.(Landroid.content.Intent;)V",
        "startPos": 7.0
      }, {
        "costTime": 1.0,
        "endPos": 19.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.MainSearchInfoHelper.getAdultNum.()I",
        "startPos": 18.0
      }, {
        "costTime": 6.0,
        "endPos": 41.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.MainSearchInfoHelper.onRoomGuestChange.(IILjava.util.List;)V",
        "startPos": 30.0
      },
      {
        "costTime": 1.0,
        "endPos": 3.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.MainSearchInfoHelper.getInstance.()Lcom.ctrip.ibu.hotel.module.main.MainSearchInfoHelper;",
        "startPos": 0.0
      }, {
        "costTime": 15.0,
        "endPos": 45.0,
        "isMainThread": true,
        "name": "com.ctrip.ibu.hotel.module.main.HotelMainActivity.lambda$gotoHotelList$11.(IILandroid.content.Intent;)V",
        "startPos": 6.0
      }])
    }, 5000)
  }


  _onReceiveMessage(moduleName, payload) {
    switch (moduleName) {
      case "appInfo":
        store.dispatch(updateAppInfo(payload.appInfo))
        break;
      case "methodCost":
        store.dispatch(updateMethodCostInfo(payload))
        break;
      default:
        break;
    }


  }


  render() {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Header style={{ paddingLeft: '10px' }}>
          <span style={{ fontWeight: 'bolder', color: '#fff', fontSize: 30 }}>MethodTraceMan.</span>
          <TagViews />
        </Header>
        <Layout>
          <BrowserRouter>
            <LeftSider />
            <RightContent />
          </BrowserRouter>
        </Layout>
      </Layout>
    );
  }
}

export default App;
