import { UPDATE_METHOD_COST, CLEAR_METHOD_COST, UPDATE_APPINFO } from "../actionTypes";

const defaultState = {
    AppInfo: null,
    methodInfoList: []
}

export default function methodProducer(state = defaultState, action) {
    switch (action.type) {
        case UPDATE_METHOD_COST:
            return Object.assign({}, state, {
                methodInfoList: action.value
            })
        case CLEAR_METHOD_COST:
            return Object.assign({}, state, {
                methodInfoList: []
            })
        case UPDATE_APPINFO:
            return Object.assign({}, state, {
                AppInfo: action.value
            })
        default:
            return state
    }
}