import * as ActionType from "./actionTypes";

//appInfo
export const updateAppInfo = (value) => ({
    type: ActionType.UPDATE_APPINFO,
    value
})



//methodCost
export const updateMethodCostInfo = (value) => ({
    type: ActionType.UPDATE_METHOD_COST,
    value
})

export const clearMethodCost = () => ({
    type: ActionType.CLEAR_METHOD_COST
})


