import React, { Component } from 'react';
import { Layout, Icon, Menu, Tag } from 'antd';
import 'antd/dist/antd.css'
import { Link } from 'react-router-dom'

const { Sider } = Layout;

class LeftSider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collapsed: false,
        }
    }

    onCollapse = collapsed => {
        console.log(collapsed);
        this.setState({ collapsed });
    };


    render() {
        return (
            <Sider theme='dark' collapsedWidth='0' collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
                <div>

                </div>
                <Menu style={{ marginTop: 25 }} theme="dark" defaultSelectedKeys={['1']} mode="inline">
                    <Menu.Item key="1"
                        disabled={false}>
                        <Link
                            to={'/methodCost'}>
                            <Icon type="file" />
                            <span>方法耗时</span>
                        </Link>

                    </Menu.Item>
                </Menu>
            </Sider>);
    }
}

export default LeftSider;



